.hero{
    display: flex;
    justify-content: space-between;
}
.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 3;
    display: flex;
    gap: 2rem ;
    flex-direction: column;
}
.right-h{
    flex: 1;
}
.the-best-ad{
    margin-top: 4rem;
    background-color: #363d42;
    border-radius: 4rem;
    width: fit-content;
    color: white;
    position: relative;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    padding: 20px 13px;
    justify-content: flex-start;
}
.the-best-ad>div{
    position: absolute;
    background-color: var(--orange);
    width: 5.4rem;
    height: 80%;
    left: 8px;
    z-index: 1;
    border-radius: 3rem;
}
.the-best-ad>span{
    z-index: 2;
}
.hero-text{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherita;
}
.shape-you{
    display: flex; 
    gap: 1.5rem;
}
.hero-text>div:nth-of-type(3) {
    font-size: 1rem;
    font-weight: 200;
    letter-spacing: 1px;
    width: 80%;
    text-transform: none;
}
.figures{
    display: flex;
    gap: 2rem;
}
.figures>div{
    display: flex;
    flex-direction: column;
}
.figures>div>span:nth-of-type(1) {
    color: white;
    font-size: 2rem;
}
.figures>div>span:nth-of-type(2) {
    color: var(--gray);
    text-transform: uppercase;
}